@import url('https://fonts.googleapis.com/css2?family=Baloo+Bhai+2&family=Josefin+Sans&family=Jost:wght@300&family=Outfit:wght@100;200;300;400;500;600;700;800;900&family=Poppins&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Outfit', sans-serif;;
}

body{
  background-color: #000;
}

h1, h4, p, a{
  color: #fff;
  text-decoration: none;
}
ul{
  list-style-type: none;
}

.btn{
  padding: 12px 18px;
  font-size: 1rem;
  text-transform: uppercase;
  background-color: yellow;
  color: #222;
  border: 1px solid #fff;
  font-weight: 600;
  cursor: pointer;
}

.btn-light{
  background: transparent;
  color: #fff;
  transition: 0.3s;
  background: rgba(255,255,255,0.2);
}

